import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

var audio = document.getElementById('myAudio');

function playAudio() {
  audio.play();
}

function pauseAudio() {
  audio.pause();
}

function stopAudio() {
  audio.pause();
  audio.currentTime = 0;
}

audio.addEventListener('ended', function () {
  // –í–∏–∫–ª–∏–∫–∞—î—Ç—å—Å—è –ø—ñ—Å–ª—è –∑–∞–≤–µ—Ä—à–µ–Ω–Ω—è –≤—ñ–¥—Ç–≤–æ—Ä–µ–Ω–Ω—è
  iziToast.show({
    title: 'Lets do it!',
    message: 'Subscribe in the bottom, and learn about new exercises!',
    position: 'bottomCenter',
    timeout: 10000, // –ß–∞—Å –≤–∏–≤–µ–¥–µ–Ω–Ω—è –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –≤ –º—ñ–ª—ñ—Å–µ–∫—É–Ω–¥–∞—Ö (5 —Å–µ–∫—É–Ω–¥ —É —Ü—å–æ–º—É –≤–∏–ø–∞–¥–∫—É)
    backgroundColor: 'var( --dark-gray-hover)',
    messageColor: 'var( --white-smoke)',
  });
});
